
.canvaswrap{
	overflow: hidden;
	margin-left: 15px;
	margin-top: 30px;
	display: inline-block;
	-ms-flex-positive: 1;
	    flex-grow: 1;
	border:2px solid #eee;
	position: relative;
}
canvas{
	position: absolute;
	top:0;
	cursor: crosshair;
}
#canvas.unable{
	cursor:not-allowed;
}
#canvas.remove{
	cursor: url("../assets/remove.png"), auto;
}
#canvas.ctrlline{
	cursor: url("../assets/ctrlline.png"), auto;
}
#canvas.move{
	cursor: move
}



